import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToasterModule, ToasterService } from 'angular2-toaster';
import { AuthService } from '../../services/auth.service';
import { ElcomService } from '../../services/elcom.service';
import { StudentService } from '../../services/student.service';
import { AccreditationService } from '../../services/accreditation.service';
import { MailService } from '../../services/mail.service';
import { SmsService } from '../../services/sms.service';
import * as $ from 'jquery';

@Component({
    selector: 'app-accreditation',
    templateUrl: './accreditation.component.html',
    styleUrls: ['./accreditation.component.css']
})
export class AccreditationComponent implements OnInit {
    isAccreditationTime: Boolean;
    message: any;
    student: any;

    public accreditationTime = {
        startingDay: '2018-01-12',
        closingDay: '2018-01-30',
        startingTime: '00:00',
        closingTime: '23:00'
    }

    constructor(
        private toasterService: ToasterService,
        public authService: AuthService,
        private router: Router,
        private elcomService: ElcomService,
        private studentService: StudentService,
        private accreditationService: AccreditationService,
        private mailService: MailService,
        private smsService: SmsService,
    ) { }

    ngOnInit() {
        var userObj = JSON.parse(localStorage.user);
        var user_name = userObj.name;
        $('#user_name').text(user_name);

        this.countAccreditedStudents();

        var dateObj = new Date();

        var month = '' + (dateObj.getUTCMonth() + 1); //months from 1-12
        if (month.length < 2) month = '0' + month;

        var day = '' + dateObj.getUTCDate();
        if (day.length < 2) day = '0' + day;

        var year = dateObj.getUTCFullYear();

        var todayDay = year + "-" + month + "-" + day;

        var hour = dateObj.getHours().toString();
        if(hour.length < 2) hour = '0' + hour;

        var minute = dateObj.getMinutes().toString();
        if(minute.length < 2) minute = '0' + minute;

        var todayTime = hour + ":" + minute;

        if(todayDay < this.accreditationTime.startingDay) {
            this.isAccreditationTime = false;
            this.message = 'Accreditation is not openned yet.';
        } else {
            if(todayDay > this.accreditationTime.closingDay) {
                this.isAccreditationTime = false;
                this.message = 'Accreditation day has passed.';
            } else {
                if(todayTime < this.accreditationTime.startingTime) {
                    this.isAccreditationTime = false;
                    this.message = 'It is not yet accreditation time.';
                } else {
                    if(todayTime > this.accreditationTime.closingTime) {
                        this.isAccreditationTime = false;
                        this.message = 'Accreditation time has elapsed.';
                    } else {
                        this.isAccreditationTime = true;
                        this.message = 'Accreditation is open.';
                        $('#matricno').trigger("focus");
                    }
                }
            }
        }
    }

    canAccredit() {
        return this.isAccreditationTime;
    }

    triggerSearch(event) {
        if(event.which == 13) {
            event.preventDefault();
            $('#searchBtn').trigger("click");
        }
    }

    searchMatric() {
        $('#searchBtn').addClass("loading disabled");
        var matricno = $('#matricno').val().replace(/\//g, "-").toUpperCase();
        if(matricno === "" || matricno === null) {
            this.toasterService.pop('error', 'Oops!', 'Please enter your matric number');
            $('#matricno').trigger('focus');
            $('#searchBtn').removeClass("loading disabled");
            return false;
        }
        // Check id student is an elcom officer first
        this.elcomService.getElcomOfficer(matricno).subscribe(
            response => {
                if(response.isElcom) {
                    this.toasterService.pop('error', 'Oops!', response.msg+ ' and cannot be accredited');
                    $('#searchBtn').removeClass("loading disabled");
                    return false;
                } else {
                    this.studentService.getStudentRecord(matricno).subscribe(
                        response => {
                            if(!response.success) {
                                this.toasterService.pop('error', 'Oops!', response.msg);
                                $('#picture').attr('src', 'data:image/png;base64,');
                                $('#accreditationForm').trigger('reset');
                                $('#submitBtn').addClass('disabled');
                                $('#searchBtn').removeClass("loading disabled");
                            } else {
                                var matricno = $('#matricno').val();
                                var lastname = response.student_info.last_name;
                                var firstname = response.student_info.first_name;
                                var email = response.student_info.email;
                                var department = response.student_info.department;
                                var course = response.student_info.programme;

                                $('#picture').attr('src', response.student_info.passport);
                                $('#firstname').val(firstname);
                                $('#lastname').val(lastname);
                                $('#email').val(email);
                                $('#department').val(department);
                                $('#course').val(course);

                                this.student = {
                                    matricno: matricno,
                                    studentName: firstname + ' ' + lastname,
                                    email: email,
                                    department: department,
                                    course: course
                                }
                                $('#submitBtn').removeClass('disabled');
                                $('#searchBtn').removeClass("loading disabled");
                            }
                        },
                        error => {
                            this.toasterService.pop('error', 'Oops!', 'Search failed due to network/server error. Please try after a while');
                            $('#searchBtn').removeClass("loading disabled");
                        }
                    );
                }
            },
            error => {
                this.toasterService.pop('error', 'Oops!', 'Search failed due to network/server error. Please try after a while');
                $('#searchBtn').removeClass("loading disabled");
            }
        );

    }

    onAccreditSubmit() {
        var pageDimmer = '<div class="ui inverted page dimmer active" id="pageDimmer"><div class="student_info"><div class="center"><div class="center"><div class="ui indeterminate large text loader"><h3>Accrediting student.<br><br>Please wait...</h3></div></div></div></div></div>';
        $('body').prepend(pageDimmer);

        var phone = $('#phone').val();
        this.student.phone = phone;

        this.accreditationService.addStudent(this.student).subscribe(
            response => {
                if(response.success) {
                    const user = {
                        name: this.student.studentName,
                        username: this.student.matricno,
                        password: this.randomPasswordString(),
                        user_role: 2
                    }
                    // Add user
                    this.authService.registerUser(user).subscribe(
                        data => {
                            if(data.success) {
                                // Send mail
                                const mailDetails = {
                                    to: this.student.email || $('#email').val(),
                                    subject: 'Accreditation Successful',
                                    text: 'You have been accredited to vote. Here are your login detail. \nUsername: '+this.student.matricno+',\n Password: '+user.password,
                                    html: ''
                                }
                                this.mailService.sendMail(mailDetails).subscribe(
                                    response => {
                                        if(response.success) {
                                            this.toasterService.pop('success', 'Success', 'Email has been sent.');
                                        } else {
                                            this.toasterService.pop('error', 'Oops!', 'Could not send mail');
                                        }
                                    },
                                    error => {
                                        this.toasterService.pop('error', 'Oops!', 'We just encountered a server error sending mail.');
                                    }
                                );

                                // Send SMS
                                const smsDetails = {
                                    recipient: this.student.phone,
                                    message: this.student.studentName+', you have been accredited to vote. Your Password to be used to login is: '+user.password
                                }
                                this.smsService.sendSMS(smsDetails).subscribe(
                                    response => {
                                        if(response.success) {
                                            this.toasterService.pop('success', 'Success', 'Password has been sent to student\'s phone');
                                        } else {
                                            this.toasterService.pop('error', 'Oops!', response.msg);
                                        }
                                    },
                                    error => {
                                        this.toasterService.pop('error', 'Oops!', 'We just encountered a server error sending SMS.');
                                    }
                                );

                                $('#picture').attr('src', 'data:image/png;base64,');
                                $('#accreditationForm').trigger('reset');
                                $('#submitBtn').addClass('disabled');
                            } else {
                                this.toasterService.pop('error', 'Oops!', 'Something went wrong adding student database');
                            }
                            this.ngOnInit();
                            $('#pageDimmer').remove();
                        },
                        error => {
                            this.toasterService.pop('error', 'Oops!', 'We just encountered a server error.');
                            $('#pageDimmer').remove();
                        }
                    );
                } else {
                    this.toasterService.pop('error', 'Oops!', 'Something went wrong');
                    $('#pageDimmer').remove();
                }
            },
            error => {
                this.toasterService.pop('error', 'Oops!', 'We just encountered a server error.');
                $('#pageDimmer').remove();
            }
        );
    }

    randomPasswordString() {
        var length = 8;
        var chars = '#aA!';
        var mask = '';
        if (chars.indexOf('a') > -1) mask += 'abcdefghijklmnopqrstuvwxyz';
        if (chars.indexOf('A') > -1) mask += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
        if (chars.indexOf('#') > -1) mask += '0123456789';
        if (chars.indexOf('!') > -1) mask += '!@#$*_-?.';
        var result = '';
        for (var i = length; i > 0; --i) result += mask[Math.round(Math.random() * (mask.length - 1))];
        return result;
    }

    checkSMSBalance() {
        $('#balanceBtn').addClass('loading');
        this.smsService.checkBalance().subscribe(
            response => {
                if(response.success) {
                    this.toasterService.pop('success', 'Success', 'SMS Balance: '+response.sms_balance);
                } else {
                    this.toasterService.pop('error', 'Oops!', response.msg);
                }
                $('#balanceBtn').removeClass('loading');
            },
            error => {
                this.toasterService.pop('error', 'Oops!', error);
                $('#balanceBtn').removeClass('loading');
            }
        );
    }

    countAccreditedStudents() {
        this.accreditationService.countAccreditedStudents().subscribe(
            response => {
                $('#accredited_students').text(response.number_of_students);
            },
            error => {
                this.toasterService.pop('error', 'Oops!', error);
            }
        );
    }

}



